Context\"\r\n                    >\r\n                        <b-form-group\r\n                            :label=\"$t('Last Name')\"\r\n                            :state=\"getValidationState(validationContext)\"\r\n                            :invalid-feedback=\"validationContext.errors[0]\"\r\n                            class=\"required-marker\"\r\n                        >\r\n                            <b-form-input\r\n                                v-model=\"formData.last_name\"\r\n                                trim\r\n                                autocomplete=\"off\"\r\n                                :state=\"getValidationState(validationContext)\"\r\n                                :disabled=\"isDisabled\"\r\n                            />\r\n                        </b-form-group>\r\n                    </validation-provider>\r\n                </b-col>\r\n            </b-form-row>\r\n            <b-form-row>\r\n                <b-col cols=\"auto\" class=\"pr-3\">\r\n                    <b-form-group :label=\"$t('Type')\">\r\n                        <b-form-radio-group\r\n                            v-model=\"formData.type\"\r\n                            :options=\"types\"\r\n                            stacked\r\n                            @change=\"changeType\"\r\n                        />\r\n                    </b-form-group>\r\n                </b-col>\r\n                <b-col\r\n                  v-if=\"formData.type == 'visitor'\"\r\n                  sm\r\n                >\r\n                    <validation-provider\r\n                        :name=\"$t('ID Number')\"\r\n                        vid=\"id_number\"\r\n                        :rules=\"{ }\"\r\n                        v-slot=\"validationContext\"\r\n                    >\r\n                        <b-form-group\r\n                            :label=\"$t('ID Number')\"\r\n                            :state=\"getValidationState(validationContext)\"\r\n                         