                <validation-provider\r\n                        :name=\"$t('Activity / Program')\"\r\n                        vid=\"activity\"\r\n                        :rules=\"{ }\"\r\n                        v-slot=\"validationContext\"\r\n                    >\r\n                        <b-form-group\r\n                            :label=\"$t('Activity / Program')\"\r\n                            :state=\"getValidationState(validationContext)\"\r\n                            :invalid-feedback=\"validationContext.errors[0]\"\r\n                        >\r\n                            <b-form-input\r\n                                ref=\"activityInput\"\r\n                                v-model=\"formData.activity\"\r\n                                trim\r\n                                autocomplete=\"off\"\r\n                                :state=\"getValidationState(validationContext)\"\r\n                                :disabled=\"isDisabled\"\r\n                            />\r\n                        </b-form-group>\r\n                    </validation-provider>\r\n                </b-col>\r\n                <b-col\r\n                  v-if=\"formData.type == 'staff' || formData.type == 'external'\"\r\n                  sm\r\n                >\r\n                    <validation-provider\r\n                        :name=\"$t('Organization')\"\r\n                        vid=\"organization\"\r\n                        :rules=\"{ }\"\r\n                        v-slot=\"validationContext\"\r\n                    >\r\n                        <b-form-group\r\n                            :label=\"$t('Organization')\"\r\n                            :state=\"getValidationState(validationContext)\"\r\n                            :invalid-feedback=\"validationContext.errors[0]\"\r\n                        >\r\n                            <b-form-input\r\n                                ref=\"organizationInput\"\r\n                                v-model=\"formData.organization\"\r\n                                trim\r\n                                autocomplete=\"off\"\r\n                                :state=\"getValidationState(validationContext)\"\r\n                                :disabled=\"isDisabled\"\r\n                            />\r\n                        </b-form-group>\r\n                    </validation-provider>\r\n                </b-col>\r\n            </b-form-row>\r\n            <p>\r\n                <b-button\r\n                    variant=\"primary\"\r\n                    type=\"submit\"\r\n                    :disabled=\"isDisabled\"\r\n                >\r\n                    <font-awesome-icon icon=\"check\" />\r\n                    {{ $t('Register') }}\r\n                </b-button>\r\n                <b-button\r\n                    variant=\"secondary\"\r\n                    type=\"button\"\r\n                    :disabled=\"isDisabled\"\r\n                    @click=\"reset\"\r\n                >\r\n                    <font-awesome-icon icon=\"eraser\" />\r\n                    {{ $t('Reset') }}\r\n                </b-button>\r\n            </p>\r\n        </b-form>\r\n    </validation-observer>\r\n</template>\r\n\r\n<script>\r\nimport formInputMixin from '@/mixins/formInputMixin'\r\nexport default {\r\n    props: {\r\n        disabled: Boolean,\r\n    },\r\n    mixins: [ formInputMixin ],\r\n    data () {\r\n        return {\r\n            formData: this.initialFormData(),\r\n            types: [\r\n                { value: 'visitor', text: this.$t('Visitor') },\r\n                { value: 'participant', text: this.$t('Participant') },\r\n                { value: 'staff', text: this.$t('Volunteer / Staff') },\r\n                { value: 'external', text: this.$t('External visitor') },\r\n            ]\r\n        }\r\n    },\r\n    computed: {\r\n        isDisabled () {\r\n            return this.disabled\r\n        }\r\n    },\r\n    watch: {\r\n        disabled (val, oldVal) {\r\n            if (!val && oldVal) {\r\n                this.$nextTick(() => this.selectFirstName())\r\n            }\r\n        }\r\n    },\r\n    mounted () {\r\n        this.focus()\r\n    },\r\n    methods: {\r\n        submit () {\r\n            this.$refs.observer.validate()\r\n                .then(valid => {\r\n                    if (valid) {\r\n                        this.onSubmit()\r\n                    }\r\n                })\r\n        },\r\n        onSubmit () {\r\n            if (this.formData.type == 'visitor') {\r\n                this.formData.activity = ''\r\n                this.formData.organization = ''\r\n            } else if (this.formData.type == 'participant') {\r\n                this.formData.id_number = ''\r\n                this.formData.place_of_residence = ''\r\n                this.formData.organization = ''\r\n            } else if (this.formData.type == 'staff' || this.formData.type == 'external') {\r\n                this.formData.id_number = ''\r\n                this.formData.place_of_residence = ''\r\n                this.formData.activity = ''\r\n            }\r\n            this.$emit('submit', this.formData)\r\n        },\r\n        reset () {\r\n            this.formData = this.initialFormData()\r\n            this.$refs.observer.reset()\r\n            this.focus()\r\n        },\r\n        initialFormData () {\r\n            return {\r\n                first_name: '',\r\n                last_name: '',\r\n                type: 'visitor',\r\n                id_number: '',\r\n                place_of_residence: '',\r\n                activity: '',\r\n                organization: ''\r\n            }\r\n        },\r\n        focus () {\r\n            this.$refs.firstNameInput.focus()\r\n        },\r\n        selectFirstName () {\r\n            this.$refs.firstNameInput.select()\r\n        },\r\n        changeType (value) {\r\n            if (value == 'staff' || value == 'external') {\r\n                this.$nextTick(() => this.$refs.organizationInput.focus())\r\n            } else if (value == 'participant') {\r\n                this.$nextTick(() => this.$refs.activityInput.focus())\r\n            } else {\r\n                this.$nextTick(() => this.$refs.idNumberInput.focus())\r\n            }\r\n        }\r\n    }\r\n}\r\n</script>\r\n\r\n<style>\r\n    .required-marker legend:after {\r\n        content: '*';\r\n        color: red;\r\n        margin-left: 3px;\r\n    }\r\n</style>\r\n"],"sourceRoot":""}]);
